import React, {Component} from "react";
import PropTypes from "prop-types";

const Switch = (props) => {

    const icon = props.showTick && <span className="g-icon g-icon--only g-icon--valid g-icon--xs-s u-spacing-right"/>;

    return (
        <div className={`opl-switch-btn u-small-spacing-l ${props.className}`} style={{height: props.height, maxWidth: "275px"}}>
            <input className="opl-switch-btn--left" type="radio" id={`${props.id}-left`}
                   name={props.id} value={props.labelLeft} checked={props.checked}/>
            <label htmlFor={`${props.id}-left`} onClick={props.onSwitchLeft}>
                            <span className="opl-switch-btn--text opl-switch-btn--text-l">
                                {icon}
                                <span>{props.labelLeft}</span>
                            </span>
            </label>
            <input className="opl-switch-btn--right" type="radio" id={`${props.id}-right`}
                   name={props.id} value={props.labelRight} checked={!props.checked}/>
            <label htmlFor={`${props.id}-right`} onClick={props.onSwitchRight}>
                            <span className="opl-switch-btn--text opl-switch-btn--text-r">
                                {icon}
                                <span>{props.labelRight}</span>
                            </span>
            </label>
            <span className="opl-switch-btn--bg"/>
        </div>
    )
};

Switch.propTypes = {
    id: PropTypes.string.isRequired,
    labelLeft: PropTypes.string.isRequired,
    labelRight: PropTypes.string.isRequired,
    checked: PropTypes.bool.isRequired,
    onSwitchLeft: PropTypes.func.isRequired,
    onSwitchRight: PropTypes.func.isRequired,
    height: PropTypes.string,
    showTick: PropTypes.bool,
    addClass: PropTypes.string
};

Switch.defaultProps = {
    height: "40px",
    showTick: true,
    className: ""
};

export default Switch;